  --color-second-black: #282828;
  --color-white: #fff;
  --color-text-button: #f87719;
  --color-button: linear-gradient( 141.22deg, #ffc226 9.4%, #f84119 91.91%);
  --color-button-hover: linear-gradient( 141.22deg, #f84119 9.4%, rgba(248, 159, 25, .68) 91.91%);
  --transition-default: 250ms cubic-bezier(.4, 0, .2, 1);
  --shadow: 1px 1px 14px rgba(1, 1, 1, .25);
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  overflow-x: hidden;
}

*, :before, :after {
  box-sizing: inherit;
}

:active, :hover, :focus {
  outline-offset: 0;
  outline: 0;
}

h1, h2, h3, h4 {
  margin: 0;
  padding: 0;
}

ul, li, a, p {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button, link {
  cursor: pointer;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 0 15px;
}

