.button-general, .button-watch-trailer, .button-rem-me, .button-my-libr {
  background: var(--color-button);
  background-color: var(--color-text-button);
  border: 1px solid var(--color-text-button);
  color: var(--color-black);
  border-radius: 74px;
  justify-content: center;
  align-items: center;
  padding: 12px 24px;
  font-family: Roboto;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.2;
  transition-property: background-color, color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.button-my-libr {
  background: var(--color-black);
  color: var(--color-white);
  border: 1px solid var(--color-text-button);
}

.button-general:hover, .button-watch-trailer:hover, .button-rem-me:hover {
  border: 1px solid var(--color-text-button);
  color: var(--color-text-button);
  background: #fff;
}

.button-my-libr:hover {
  background: var(--color-white);
  color: var(--color-text-button);
  border: none;
}

.button-round-pag, .button-round-search {
  width: 24px;
  height: 24px;
  background: var(--color-button);
  color: #fff;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-family: Roboto;
  font-size: 10px;
  font-weight: 400;
  line-height: 1;
  transition-property: background-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.button-round-search {
  width: 40px;
  height: 40px;
  fill: var(--color-white);
}

.button-round-pag:hover, .button-round-search:hover {
  background: var(--color-button-hover);
  color: var(--color-text-header-l);
  fill: var(--color-text-header-l);
}

.button-up {
  opacity: 0;
  visibility: hidden;
  z-index: 8;
  background-color: var(--color-button);
  border: 2px solid var(--color-text-button);
  width: 50px;
  height: 50px;
  text-align: center;
  color: #494848;
  border-radius: 25px;
  justify-content: center;
  align-items: center;
  font-family: Film P3;
  font-size: 64px;
  font-weight: 700;
  transition: background-color .3s, opacity .5s, visibility .5s;
  animation: animatebut 3s 1s infinite;
  display: inline-flex;
  position: fixed;
  bottom: 50px;
  right: 50px;
}

.button-up:hover, .button-up:focus {
  background-color: var(--color-text-button);
  animation: animatebut step-start;
}

.arrow-up {
  opacity: 0;
  visibility: hidden;
}

.arrow-up span {
  z-index: 3;
  width: 15px;
  height: 15px;
  border-left: 3px solid var(--color-button);
  border-top: 3px solid var(--color-text-button);
  text-shadow: 1px 1px 2px #494848;
  margin: -2px;
  animation: arrow-up 3s infinite;
  display: block;
  position: fixed;
  bottom: 70px;
  right: 60px;
}

.arrow-up span:nth-child(2) {
  animation-delay: -.1s;
  bottom: 90px;
  right: 70px;
}

.arrow-up span:nth-child(3) {
  animation-delay: -.2s;
  bottom: 75px;
  right: 80px;
}

.button-up.button-up-show, .arrow-up.button-up-show {
  opacity: 1;
  visibility: visible;
}

@keyframes arrow-up {
  0% {
    opacity: 0;
    transform: rotate(45deg)translate(10px, 10px);
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: rotate(45deg)translate(-10px, -10px);
  }
}

@keyframes animatebut {
  0% {
    transform: rotateY(0deg);
  }

  50% {
    transform: rotateY(90deg);
  }

  100% {
    transform: rotateY(0deg);
  }
}

@media screen and (min-width: 768px) {
  .button-general, .button-watch-trailer, .button-rem-me {
